body{
    background: rgb(54, 177, 164);
}
.hero-container{
    background: turquoise;
    height: 90vh;
    width:100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
    object-fit: contain;
}
.intro{
    color: beige;
    background-color: rgba(54, 52, 75, 0.8);
    width: 100%;
    height: 600px;
    text-align: center;
    margin: 60px 0px 50px;
}

#description{
    padding: 20px;
    margin: 0px 15vw;
    text-align: center;
    font-size: 1.2em;
    /* border: 2px solid rgba(54, 52, 75, 0.8); */
}

.buttons{
    display: flex;
    flex-direction: row;
}

.btn{
    margin: 50px 10vw;
}

.type {
    display:inline-flex;
    padding: 3vh 0px 7vh;
    font-size: 0.8em;
  }
  
  .type span {
    word-break: break-all;
    height: 1.2em;
    width:0%;
    overflow: hidden;
    animation:t 2s linear infinite alternate;
  }
  .type span:before {
    content:" ";
    display:inline-block;
  }
  
  @keyframes t{
    90%,100% {width:100%}
  }
  
  
  /**/
  
  h1 {
    display:table;
    margin:auto;
  }
  
  p {
    font-size:25px;
  }
  
  p + p {
    font-style:italic;
  }

  @keyframes t{
    90%,100% {width:100%}
  }
  
  @keyframes c{
    0%,100%{box-shadow:5px 0 0 #0000}
    50%    {box-shadow:5px 0 0 red  }
  }

  #hello{
    font-size: 3.4em;
    color: beige;
  }
  #name{
    color: rgb(95, 200, 189);
    font-size: 4em;
    position: relative;
    top: 3px;
  }

  #helloName{
    padding-top: 19vh;
  }
